.about-banner {
	width: 100%;
	height: 250px;
	background: url("../public/img/about-banner.jpg") no-repeat center/cover;
}

.center .sort {
	margin-top: 50px;
	width: 100%;
	height: 40px;
	display: flex;
	flex-flow: row column;
	justify-content: space-around;
}

.center .sort li {
	width: 100%;
	line-height: 40px;
	border: 1px solid #888;
}

.center .sort li a {
	color: #333;
	display: block;
	text-align: center;
}

.center .product {
	width: 100%;
	height: auto;
	margin: 70px 0;
	display: flex;
	justify-content: flex-start;
	flex-flow: row wrap;
	position: relative;
}

.center .product .product-detail-nutrient {
	width: 25%;
	position: relative;
}

.center .product .product-detail-nutrient .product-detail {
	width: 100%;
	padding: 20px;
	box-sizing: border-box;
	/* border: 1px solid #ebebeb; */
}

.center .product .product-detail-nutrient .product-detail img {
	width: 100%;
	border: 1px solid #ebebeb;
}

.center .product .product-detail-nutrient .product-detail p {
	width: 100%;
	text-align: center;
	font-size: 18px;
	line-height: 20px;
}

.center .product .product-detail-nutrient .nutrient {
	width: 100%;
	height: 100%;
	padding: 20px;
	box-sizing: border-box;
	position: absolute;
	top: 0;
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	align-items: center;
	opacity: 0;
	background-color: rgb(250, 226, 90);
	transition: all 0.5s;
}
.center .product .product-detail-nutrient .nutrient:hover {
	opacity: 0.8;
}

.center .product .product-detail-nutrient .nutrient h3 {
	width: 100%;
	text-align: center;
}

.center .product .product-detail-nutrient .nutrient .bar {
	width: 80%;
	height: 0;
	border: 1px solid #333;
	margin: 20px 0;
}

.center .product .product-detail-nutrient .nutrient p {
	width: 100%;
	text-align: center;
}

.border {
	background-color: rgb(250, 226, 90);
	border: 0;
}
