.bitter-section-01 {
	width: 100%;
	height: 800px;
	background-color: rgb(250, 226, 90);
	background-image: url(../public/img/bitter.png);
	background-repeat: no-repeat;
	background-position-x: 80%;
	background-position-y: center;
	display: flex;
	justify-content: center;
	flex-flow: column nowrap;
}

.bitter-section-01 h3 {
	font-size: 35px;
	padding: 0 0 0 200px;
	line-height: 50px;
}
.bitter-section-01 h1 {
	font-size: 50px;
	padding: 0 0 0 200px;
	line-height: 50px;
}

.bitter-section-01 p {
	width: 40%;
	padding: 0 0 0 200px;
	height: 100px;
	display: flex;
	font-size: 20px;
	align-items: center;
}

.roasting {
	width: 100%;
	height: 400px;
	display: flex;
	justify-content: center;
	flex-flow: column nowrap;
}

.roasting h1 {
	text-align: center;
	font-size: 50px;
	line-height: 100px;
}

.roasting p {
	width: 40%;
	margin: 0 auto;
	text-align: center;
	font-weight: 450;
}
