@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap");

body,
html {
	width: 100%;
	height: 100vh;
}

* {
	margin: 0;
	padding: 0;
	font-family: "Noto Sans KR", sans-serif;
	font-optical-sizing: auto;
	font-style: normal;
}

ul,
ol {
	list-style: none;
}

a {
	text-decoration: none;
	display: block;
	cursor: pointer;
}

#wrap {
	width: 100%;
	height: 100%;
}
.center {
	width: 90%;
	margin: 0 auto;
	min-width: 1500px;
	max-width: 1700px;
}

.swiper-slide {
	text-align: center;
	font-size: 18px;
	/* background: #fff; */
	display: flex;
	justify-content: center;
	align-items: center;
}

.swiper-slide img {
	/* display: block; */
	width: 100%;
	height: 100%;
	object-fit: cover;
}

header {
	width: 100%;
	height: 50px;
	background-color: rgb(241, 209, 64);
	position: relative;
	z-index: 50;
}

header .center {
	display: flex;
	justify-content: space-between;
}

header .center h1 {
	width: 200px;
	height: 100%;
	line-height: 50px;
}
header .center h1 a {
	width: 100%;
	height: 100%;
	line-height: 50px;
}

header .center h1 a img {
	width: 100%;
	height: 100%;
	line-height: 50px;
}

header .center nav {
	width: 600px;
	height: 100%;
}

header .center nav ul {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: space-between;
	flex-flow: row nowrap;
	line-height: 50px;
}

header .center nav ul > li {
	width: 120px;
	height: 100%;
	text-align: center;
	background-color: rgb(241, 209, 64);
}
header .center nav ul ol {
	width: 120px;
	height: auto;
}

header .center nav ul ol li {
	height: 50px;
	width: 120px;
}

header .center nav ul li a {
	color: black;
}

#shop {
	width: 100%;
	height: auto;
}

#shop .center {
	height: 800px;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-flow: column nowrap;
}

#shop .center h3 {
	width: 100%;
	font-size: 40px;
	line-height: 70px;
}

#shop .center .bar {
	width: 30px;
	height: 0;
	border: 1px solid black;
	margin: 0 auto;
}

#shop .center > p {
	width: 100%;
	font-size: 20px;
	line-height: 50px;
	position: relative;
}

#shop .center a {
	width: 150px;
	background-color: black;
	height: 30px;
	color: #fff;
	line-height: 30px;
	border-radius: 5px;
	margin: 0 auto;
}

#shop .center .shop-list {
	width: 90%;
	height: 400px;
	align-items: center;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
}

#shop .center .shop-list div {
	width: 250px;
	height: 250px;
	position: relative;
}

#shop .center .shop-list .shop-gang-seo {
	background: url("../public/img/강서.jpg") no-repeat center/cover;
}

#shop .center .shop-list .shop-sang-myeong {
	background: url("../public/img/상명대.jpg") no-repeat center/cover;
}

#shop .center .shop-list .shop-su-won {
	background: url("../public/img/수원.jpg") no-repeat center/cover;
}

#shop .center .shop-list .shop-yong-in {
	background: url("../public/img/용인.jpg") no-repeat center/cover;
}

#shop .center .shop-list div p {
	position: absolute;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.8);
	color: white;
	width: 100%;
}

#shop .more {
	position: relative;
	width: 100%;
	height: 800px;
	background: url("../public/img/sub-banner01.jpg") no-repeat center/cover;
	color: #fff;
}

#shop .more .bg-black {
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.4);
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;
}

#shop .more .bg-black h3 {
	width: 100%;
	text-align: center;
	line-height: 70px;
	font-size: 30px;
}

#shop .more .bg-black .bar {
	width: 50px;
	height: 0;
	border: 1px solid white;
}

#shop .more .bg-black p {
	font-size: 20px;
	line-height: 100px;
}

#shop .more .bg-black .more-btn {
	width: 150px;
	height: 40px;
	background-color: rgba(0, 0, 0, 0.7);
	text-align: center;
	line-height: 40px;
	border-radius: 5px;
	font-size: 17px;
}

footer {
	width: 100%;
	height: 150px;
	background-color: rgb(36, 37, 45);
	position: relative;
	bottom: 0;
	color: #ebebeb;
	font-weight: 300;
}

footer .f-top {
	width: 100%;
	height: 100px;
	display: flex;
	align-items: center;
}

footer .f-top .f-left {
	width: 50%;
}

footer .f-top .f-right {
	width: 50%;
}

footer .f-bottom {
	width: 100%;
	text-align: center;
	line-height: 50px;
}
