.about-banner {
	width: 100%;
	height: 250px;
	background: url("../public/img/about-banner.jpg") no-repeat center/cover;
}

.center .about-section-01 {
	width: 100%;
	text-align: center;
	height: 800px;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;
}

.center .about-section-01 h3 {
	width: 100%;
	text-align: center;
	font-size: 90px;
	line-height: 120px;
}

.center .about-section-01 p {
	font-size: 90px;
	letter-spacing: 25px;
	text-align: center;
	line-height: 120px;
}

.center .about-section-02 {
	width: 100%;
	text-align: center;
	height: 1000px;
}
.center .about-section-02 .p {
	width: 100%;
	line-height: 40px;
	height: 40px;
	text-align: center;
	font-size: 25px;
}

.center .about-section-02 h3 {
	width: 100%;
	font-size: 45px;
	line-height: 60px;
}

.center .about-section-02 .history {
	width: 100%;
	display: flex;
	height: 900px;
}

.center .about-section-02 .history .a-left {
	width: 50%;
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	height: 100%;
}

.center .about-section-02 .history .a-left h3 {
	width: 100%;
	padding-top: 40px;
	font-weight: 900;
	font-size: 70px;
	text-align: left;
	padding-left: 200px;
	color: rgb(245, 220, 58);
	box-sizing: border-box;
	padding-bottom: 20px;
}

.center .about-section-02 .history .a-left .bar {
	width: 50px;
	height: 0;
	border: 1px solid black;
	margin-left: 200px;
	box-sizing: border-box;
	margin-bottom: 20px;
}
.center .about-section-02 .history .a-left ul li {
	line-height: 30px;
}
.center .about-section-02 .history .a-right {
	width: 50%;
	height: 100%;
	display: flex;
	align-items: center;
	height: 100%;
}

.center .about-section-02 .history .a-right img {
	width: 80%;
}

.center .about-section-03 {
	width: 100%;
	height: 800px;
	background: url("https://composecoffee.com/modules/page/skins/about/img/05-01.png") no-repeat center;
	display: flex;
	align-items: flex-end;
}

.center .about-section-03 .tech {
	width: 100%;
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-around;
	padding-bottom: 50px;
}

.center .about-section-03 .tech > div {
	width: 30%;
	height: 250px;
	background-color: rgb(245, 220, 58);
	text-align: center;
	border-radius: 0 30px 0 0;
}

.center .about-section-03 .tech div h3 {
	line-height: 50px;
	font-size: 25px;
	text-align: left;
	padding-top: 10px;
	padding-left: 40px;
	box-sizing: border-box;
}

.center .about-section-03 .tech div div {
	width: 100%;
	height: 180px;
	display: flex;
	flex-flow: column nowrap;
	justify-content: flex-start;
	padding-top: 20px;
	box-sizing: border-box;
}

.center .about-section-03 .tech div div p {
	height: 30px;
	text-align: left;
	padding-left: 40px;
	padding-right: 40px;
	box-sizing: border-box;
}
